.detail-container{
    width: 80%;
    margin: auto;
    padding: 10px 0;
    /* padding-top:20px; */
 }
 
 .detail-div{
     display: flex;
     width: 100%;
     justify-content: space-between;
     border: 1px solid black;
 }
 
 .detail-div-1{
    /* width: 50%;
    overflow: hidden; */
    padding-top: 50px;
    display: 'flex';
    justify-content: center;
    align-items: center;
    width:  50%;
    height: 1000px;
    border: 1px solid black;
 
 }
 
 .detail-img {
     width: 100%;
     height: 100%;
     object-fit: cover;
     margin: auto;
     border: 1px solid black;
 }
 
 .detail-div-2{
     width: 50%;
     text-align: left;
     padding: 0 30px;
     border: 1px solid black;
 }
 .detail-title{
     padding: 30px 0;
     font-weight: bold;
     width: 100%;
     border-bottom: 1px solid black;
 }
 .detail-price{
     padding: 10px 0;
     color: black;
     border-bottom: 0.5px solid rgba(128, 128, 128, 0.3);
 }
 .detail-price-halin{
     font-size: 12px;
     color: rgba(0, 0, 0, 0.5);
     position: relative;
 }
 .detail-priceinfo{
     position: absolute;
     top: 110px;
     left: 880px;
     z-index: 5;
 
     width: 500px;
     height: 200px;
     border: 1px solid black;
     background-color: white;
     font-size: 12px;
 }
 
 .detail-price-line{
     text-decoration-line: line-through;
 }
 .detail-price-price{
    color:red;
    font-size: 20px;
    font-weight: bold;
 }
 .detail-count{
     font-size: 15px;
     padding: 10px 0;
 }
 
 input[type='number']{
     height: 30px;
 }
 
 input[type='number']::-webkit-outer-spin-button,
 input[type='number']::-webkit-inner-spin-button {
       padding: 10px;
 }
 
 .detail-button{
     padding: 30px 0;
 }
 
 .productScore{
     text-align: left;
 }
 
 .productReq{
     border: solid 1px black;
     text-align: left;
     padding: 40px 40px;
 }
 
 .productReq-top{
     display: flex;
     justify-content: space-between;
 }
 
 .detail-div-3{
     display: flex;
     justify-content: space-between;
 }
 .detail-delivery{
     display: flex;
     align-items: center;
 }

 .delivery-price{
    font-size: 12px;
 }
 
 @media (max-width:768px) {
     .detail-div{
         display: flex;
         flex-direction:  column;
     }
     .detail-div-1{
         width:100%;
         height: 500px;
     }
     .detail-div-2{
         width: 95%;
         height: 500px;
         padding: 0;
         margin: auto;
     }
 }
 