.item_img{
    height: 290px;
    width: 290px;
    display: flex;
    align-items: center;
    transition: transform 0.3s ease;
}

.item_img:hover {
    transform: scale(1.1);
}

.items_line{
    display: flex;
    align-items: center;
    padding: 3px;
    gap: 10px;
    flex-wrap: wrap;
    flex: 1; /* 남은 공간을 차지하도록 설정 */
}

.items-container{
    border: 1px solid black;
    width: 1120px;
    margin: 0 auto;
    border: none;
}

.line{
    height: 370px;
    width: 290px;
    border: 1.5px solid black;
    overflow: hidden;
    line-height: 20px;
}

.line:hover{
    border: 1.5px solid blue;
    box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.3);
    font-size: 20px;
    text-decoration: underline blue;
    color: blue;
}

.text-title{
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    text-decoration: none;
    color: black;
}

.line:hover .text-title {
    text-decoration: underline blue;
    color: blue;
    font-size: 20px; /* 텍스트 크기 변경 */
}

.items-line-container {
    display: flex;
    border: none;
}

.LineBanner {
    /* 라인 배너에 대한 스타일 */
    border: none;
    /* 다른 필요한 스타일 */
}

@media (min-width: 700px) and (max-width:970px) {
    .container{
        width: 600px;
    }
    .item_img{
        height: 210px;
        width: 250px;
    }
    .line{
        height: 270px;
        width: 250px;
    }
  }

@media (max-width: 700px) {
    .container{
        width: 400px;
    }
    .item_img{
        height: 150px;
        width: 150px;
    }
    .line{
        height: 220px;
        width: 160px;
        font-size: 13px;
    }
    .line:hover .text-title {
        text-decoration: underline blue;
        color: blue;
        font-size: 15px; /* 텍스트 크기 변경 */
    }
  }