.detail-priceinfo-full{
	position: absolute;
	width: 100%;
	height: 100%;
	background-color: none;
	z-index: 2;
}

.priceinfo-title{
	display: flex;
	justify-content: space-between;
	align-items: center;
	border-bottom: 2px solid lightgray;
  padding: 16px;
	font-size: 15px;
	font-weight: bold;
}

.close-modal-icon{
	width: 20px;
	height: 20px;
	background-image: url('https://img1a.coupangcdn.com/image/dragonstone/sdp/popup/rdsClose@2x.png');
  cursor: pointer;
	background-size: 20px 20px;
}
.priceinfo-container{
   display: flex;
	 text-align: left;
	 padding-top: 10px;
}
.priceinfo-price{
	width:200px;
	padding-left: 10px;
}
