.slider-item {
    width: 1903px; /* 슬라이더 아이템의 너비를 100%로 설정 */
    height: 500px; /* 원하는 높이로 설정 */
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box; /* 패딩과 보더를 포함한 전체 너비와 높이 계산 */
}

.slider-item img {
    width: 100%; /* 이미지의 너비를 슬라이더 아이템에 맞춤 */
    height: 100%; /* 이미지의 높이를 슬라이더 아이템에 맞춤 */
    object-fit: cover; /* 이미지를 컨테이너에 맞춰 자름 */
}
